.expandable:hover {
  cursor: pointer; }

/*--Header--*/
/* a3a3a3 */
.Header {
  /* background: linear-gradient(to bottom, #e6e6e6 0%, #c3c3c3 100%); */
  /* background: white; */
  display: block;
  font-family: consoleFont, sans-serif;
  border: 0.15em solid black;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  flex: 0 1 auto;
  /* The above is shorthand for:
  flex-grow: 0,
  flex-shrink: 1,
  flex-basis: auto
  */
  font-size: 1.0em;
  height: 1.8em;
  color: white;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  box-shadow: 0px 0px 20px #000000;
  z-index: 500; }
  .Header .selected {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #fc03ad), color-stop(100%, #335ef6));
    color: #fff;
    border-bottom: 0.1em solid #5978F5;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    height: 30px; }
  .Header ul {
    margin: 0 5px;
    padding: 0 3px; }
  .Header li {
    display: inline-block;
    line-height: 1.8em;
    padding: 0 0.5em;
    position: relative;
    cursor: default; }
  .Header .left {
    float: left; }
  .Header .right {
    float: right; }
  .Header a {
    color: #000;
    text-decoration: none; }
  .Header .submenu {
    display: none; }
    .Header .submenu input[type="text"] {
      border-radius: 2em;
      border: 1px solid #333;
      height: 2em;
      padding: 0 10px; }
    .Header .submenu input[type="range"] {
      transform: rotate(-90deg);
      margin-left: -50px;
      margin-top: 65px; }
    .Header .submenu ul {
      background: black;
      border: white solid 2px;
      border-radius: 0 8px 8px 8px;
      color: white;
      font-size: 14px;
      left: 0px;
      margin: 0 !important;
      overflow: hidden;
      padding: 0px 5px 0px 0px;
      position: absolute;
      top: 32px;
      width: auto;
      white-space: nowrap;
      z-index: 200; }
    .Header .submenu ul.leftBar {
      right: 0px;
      left: auto;
      border-radius: 8px 0 8px 8px; }
    .Header .submenu ul.white {
      background: #fff;
      color: #333; }
    .Header .submenu ul.volume {
      height: 10em;
      width: 2.2em;
      margin: 0;
      padding: 0;
      position: absolute;
      overflow: visible; }
    .Header .submenu ul.volume li {
      margin: 0;
      padding: 0; }
    .Header .submenu ul li {
      display: block !important;
      vertical-align: middle;
      float: none;
      margin-right: 75px;
      font-weight: 400;
      padding: 3px 3px 3px 15px;
      width: 100%;
      color: white; }
    .Header .submenu ul li.spacer {
      background: rgba(211, 211, 211, 0.8);
      height: 1px !important;
      margin: 2px 0;
      padding: 0;
      width: 102%;
      position: relative; }
    .Header .submenu ul li.spacer:hover {
      background: rgba(211, 211, 211, 0.8); }
    .Header .submenu li:hover:not(.volume) {
      background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #fc03ad), color-stop(100%, #335ef6));
      color: #fff; }
    .Header .submenu .hasSubMenu:after {
      content: "\f04b";
      font-family: FontAwesome;
      font-size: 0.8em;
      display: inline-block;
      position: absolute;
      right: 25px; }

.BackHeader {
  position: absolute;
  background: black;
  z-index: 499;
  width: 100%;
  height: 10px; }

/*--Menus--*/
.visible {
  display: block !important; }

.shortcut:after {
  content: attr(shortcut);
  float: right;
  padding-right: 20px; }

.search {
  padding: 5px 10px !important; }

.noChoice {
  color: #adadad; }

.noChoice:hover {
  background: #fff !important;
  color: #adadad !important; }

#volume-icon-li {
  width: 1em; }

.focus img {
  border: 2px solid rgba(225, 225, 225, 0.5) !important;
  border-radius: 6px; }

span.focus:after {
  background: #146ece;
  border-radius: 15px;
  text-shadow: none !important; }

span#pageTitle {
  font-weight: bold; }
