.MacbookAir {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .MacbookAir .react-player {
    position: absolute;
    top: 0;
    margin: auto;
    z-index: -100;
    overflow: hidden; }
  .MacbookAir .mainContent {
    /* made titlebar position relative to fit this under without padding */
    /* padding-top: 22px; */
    z-index: 10;
    pointer-events: none; }
  .MacbookAir .sliderBox {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 18px; }
  .MacbookAir .clouds {
    /*  made 0, was -200, because clouds were behind app-frame-box thing */
    z-index: 0;
    position: fixed;
    min-width: 100%;
    min-height: 100%; }
  .MacbookAir span.emoji-slider {
    font-size: 35px;
    line-height: 28px; }
